<template>
  <div 
    class="mhz-message"
    :style="currentStyle"
    >
    <div class="message-main" v-for="item in msgList" :key="item.name">
      <div class="message-content">
        <m-icon :name="item.icon" :color='item.color'></m-icon>
        {{item.content}}
      </div>
    </div>
  </div>
</template>

<script>
import mIcon from "../../../components/Icon/main/icon.vue"
const POSITIN_STYLES = {
  leftTop: 'top: 24px; left: 24px;',
  leftCenter: 'top: 50%; left: 24px; transform: translateY(-50%);',
  leftBottom: 'bottom: 24px; left: 24px;',
  topCenter: 'top: 24px; left: 50%; transform: translateX(-50%);',
  center: 'top: 50%; left: 50%; transform: translateX(-50%) translateY(-50%);',
  bottomCenter: 'bottom: 24px; left: 50%; transform: translateX(-50%)',
  rightTop: 'top: 24px; right: 24px;',
  rightCenter: 'top: 50%; right: 24px; transform: translateY(-50%);',
  rightBottom: 'bottom: 24px; right: 24px;',
}

let seed = 0;
function getUuid() {
  return 'alert' + (seed ++);
}

export default {
  name: 'mMessage',
  props: {},
  components: { mIcon },
  data() {
    return {
      msgList: [],
      currentStyle: ''
    };
  },
  watch: {},
  computed: {},
  created() {},
  mounted() {},
  methods: {
    add(type, msg) {
      const colors = {
        info: '#409EFF',
        success: '#67C23A',
        warning: '#E6A23C',
        danger: '#F56C6C',
      };
      const icons = {
        info: 'info-circle',
        success: 'zhengque',
        warning: 'jinggao',
        danger: 'cuowu',
      };
      const name = getUuid();

      let _msg = Object.assign({
        type,
        color: colors[type],
        icon: icons[type],
        name
      }, msg)

      this.currentStyle = POSITIN_STYLES[msg.position];
      this.msgList.push(_msg);

      //  定时移除
      const duration = msg.duration;
      setTimeout(() => {
        this.remove(name);
      }, duration * 1000)
    },
    remove(name) {
      const msgList = this.msgList;

      for(let i = 0; i < msgList.length; i ++) {
        if (msgList[i].name === name) {
          this.msgList.splice(i, 1);
          break;
        }
      }
    }
  },
};
</script>